.map {
  height: 500px;
  padding: 1rem;
  border-radius: 20px;
  margin-top: 16px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);

  .leaflet-container {
    height: 100%;
  }

  .info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
    img {
      width: 100px;
      border-radius: 5px;
    }
  }

  .info-confirmed,
  .info-recovered,
  .info-deaths {
    font-size: 16px;
    margin-top: 5px;
  }
}
